import React, { Component } from "react";
import { AppContext } from "../AppProvider";
import { GAME_TYPES, PLAYER_TURNS, ICON_CHARS } from "../common";
import "./Main.css";
import cubeLogo from "../assets/logo.png";

const ICON_PLACE_HOLDDER = (
    <img
        className="svgPlaying"
        style={{
            position: "absolute",
            opacity: "0.0000001",
            pointerEvents: "none",
        }}
        src={cubeLogo}
        alt="Cube Logo"
    />
);

const Cell = (props) => {
    return (
        <AppContext.Consumer>
            {(context) => {
                const value = context.cells[props.index];
                const icon =
                    value !== null
                        ? value === 1
                            ? ICON_CHARS[(context.gamesPlayed % 3) + 1]
                            : ICON_CHARS[value]
                        : ICON_PLACE_HOLDDER;
                const isDoneClass = icon !== ICON_PLACE_HOLDDER ? "done" : "";

                return (
                    <button
                        className={`cell cell-${props.index} ${isDoneClass}`}
                        onClick={() => context.humanPlay(props.index)}
                    >
                        {icon}
                    </button>
                );
            }}
        </AppContext.Consumer>
    );
};

class Board extends Component {
    constructor(props) {
        super(props);
        this.boardRef = React.createRef();
    }

    componentDidUpdate() {
        if (this.context.gameState.position !== "") {
            setTimeout(() => {
                this.boardRef.current.classList.add("full");
            }, 50);
        } else {
            this.boardRef.current.classList.remove("full");
        }
    }

    render() {
        return (
            <div
                className={`board ${this.context.gameState.position}`}
                ref={this.boardRef}
            >
                <div className="board-row">
                    <Cell index={0} />
                    <Cell index={1} />
                    <Cell index={2} />
                </div>

                <div className="board-row">
                    <Cell index={3} />
                    <Cell index={4} />
                    <Cell index={5} />
                </div>

                <div className="board-row">
                    <Cell index={6} />
                    <Cell index={7} />
                    <Cell index={8} />
                </div>
            </div>
        );
    }
}
Board.contextType = AppContext;

class Main extends Component {
    render() {
        return (
            <main className="main">
                <Board />
            </main>
        );
    }
}
Main.contextType = AppContext;

export default Main;
